import React, {useState,useEffect, createContext, useContext} from 'react';
import Navbar from './navbar'
import BalanceCard from './BalanceCard'
import TransactionBtnGrp from './TransactionBtnGrp'
import Transactions from './Transactions'
import Box from '@mui/material/Box'
import Container from '@mui/material/Container'
import { Typography } from '@mui/material'
import {AuthContext} from '../../App'

export default function Dashboard(){
    let auth = useContext(AuthContext);

    useEffect(() => {
        auth.getTransactions()
    }, [])

    return(
        <Box>
            <Navbar/>
            <Container sx = {{mt:5}}>
                <Typography capitalize variant = "h4" textAlign= "center">Welcome, {auth.user.firstName} {auth.user.lastName}!</Typography>
            </Container >
            <Container sx = {{mt: 10, width: '50%',['@media (max-width:960px)']: { width: '80%'}, ['@media (max-width:520px)']: { width: '100%'}}}>
                <BalanceCard/>
                <Box sx = {{display : 'flex',['@media (max-width:400px)']: { flexDirection: 'column', alignItems: 'center'}, justifyContent:'space-between'}}>
                    <TransactionBtnGrp/>
                </Box>
            </Container>
            <Transactions/>
        </Box>
    )
} 