import React, {useContext} from 'react';
import CustomButton from '../../Components/Button';
import {CustomTextField} from '../../Components/TextField'
import {PasswordField} from '../../Components/TextField'
import {Link} from 'react-router-dom'
import Grid from '@mui/material/Grid';
import CustomForm from '../../Components/Form'
import {AuthContext} from '../../App';
import Box from '@mui/material/Box';
import {useNavigate, useLocation} from "react-router-dom";
import {useFormik} from 'formik'
import {signupSchema} from '../../Components/Formik';
import Alert from '@mui/material/Alert';
import {constants} from '../../Utilities/constants'


export default function SignUp() {
    let auth = useContext(AuthContext);
    let navigate = useNavigate();
    let location = useLocation();  
    

    const formik = useFormik({
        initialValues: {
          email: '',
          password: '',
          firstName: '',
          lastName: ''
        },
        validationSchema: signupSchema,
        onSubmit: (data) => {
            console.log(data)
            let apiLink = `${constants.baseURL}/api/v1/auth/signup`
            let credentials = {data, apiLink}
            let { from } = location.state || { from: { pathname: "/login" } };
            auth.signupUser(credentials, ()=>{
                navigate(from)
            })  
        },
      });


  return (
    <CustomForm title = 'Sign Up'>
        <Box component="form" onSubmit={formik.handleSubmit} sx={{ mt: 1 }}>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                    <CustomTextField
                        id = 'firstName' 
                        name = 'firstName' 
                        label = 'First name' 
                        value={formik.values.firstName}
                        onChange={formik.handleChange}
                        error={formik.touched.firstName && Boolean(formik.errors.firstName)}
                        helperText = {formik.touched.firstName && formik.errors.firstName}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <CustomTextField
                        id = 'lastName' 
                        name = 'lastName' 
                        label = 'Last name' 
                        value={formik.values.lastName}
                        onChange={formik.handleChange}
                        error={formik.touched.lastName && Boolean(formik.errors.lastName)}
                        helperText = {formik.touched.lastName && formik.errors.lastName}
                    />
                </Grid>
                <Grid item xs={12}>
                    <CustomTextField 
                        id = 'email' 
                        name = 'email' 
                        label = 'Email address'
                        value={formik.values.email}
                        onChange={formik.handleChange}
                        error={formik.touched.email && Boolean(formik.errors.email)}
                        helperText = {formik.touched.email && formik.errors.email}
                    />
                </Grid>
                <Grid item xs={12}>
                    <PasswordField 
                        id = 'password' 
                        name = 'password' 
                        label = 'Password'
                        value={formik.values.password}
                        onChange={formik.handleChange}
                        error={formik.touched.password && Boolean(formik.errors.password)}
                        helperText = {formik.touched.password && formik.errors.password}
                    />
                </Grid>
            </Grid>
            <CustomButton label = {auth.loading?"Loading...":"Sign up"} disabled = {auth.loading}/>
            {auth.errMsg &&(
                <Box sx = {{mb : 2}}>
                <Alert variant="outlined" severity="error">
                    {auth.errMsg}
                </Alert>
                </Box>
            )}
            <Grid container justifyContent="flex-end">
                <Grid item>
                <Link className = 'link' to = '/login' variant="body2">
                    Already have an account? Sign in
                </Link>
                </Grid>
            </Grid>
        </Box>
    </CustomForm>
  );
}