import React, {useState, useEffect} from 'react'
import {AuthContext} from '../App'
import useRequest from '../Hooks/useRequest'
import useStorage from '../Hooks/useStorage'
import Numbro from '../Utilities/Numbro'
import {constants} from '../Utilities/constants'


export default function ProvideAuth({children}){
  const auth = useProvideAuth()
  return(
    <AuthContext.Provider value={auth}>
      {children}
    </AuthContext.Provider>
  )
}


function useProvideAuth(){
  const [user, setUser] = useState(null)
  const [appLoad, setAppLoad] = useState(true)
  const[errMsg, setErrMsg] = useState(null)
  const[response, setResponse] = useState(null)
  const[loading, setLoading] = useState(false)
  const [transactions, setTransactions] = useState(null)
  const [txnLoading, setTxnLoading] = useState(true)
  const request = useRequest()
  const storage = useStorage()

  useEffect(() => {
    init()
  },[])


  const init = async () => {
    const value = await storage.getUser()
    setUser(value)
    setAppLoad(false)
  }

  const loginUser = async (details, cb) =>{
    setErrMsg(null)
    setLoading(true)
    request.sendRequest(details)
    .then(async(res)=>{
      console.log('response is', res.user)
      await storage.storeUser(res.user)
      setUser(res.user)
      cb()
      setLoading(false)
    }).catch((err)=>{
      setLoading(false)
      if(err.response){
        console.log(err.response)
        setErrMsg(err.response.statusText)  
      }else{
        console.log(err.message);
        setErrMsg(err.message)
      }
    })
  }

  const logoutUser = cb =>{
    setUser(null)
    storage.removeUser()
    setErrMsg(null)
    setLoading(false)
    setResponse(null)
    setTransactions(null)
    cb()
  }

  const signupUser = async (details, cb) =>{
    setErrMsg(null)
    setLoading(true)
    request.sendRequest(details)
    .then((res)=>{
      console.log(res)
      cb()
      setLoading(false)
    }).catch((err)=>{
      setLoading(false)
      if(err.response){
        console.log(err.response)
        setErrMsg(err.response.statusText)  
      }else{
        console.log(err.message);
        setErrMsg(err.message)
      }
    })
  }

  const transact = async (details, cb) =>{
    setErrMsg(null)
    setLoading(true)
    setResponse(null)
    request.sendRequest(details)
    .then(async(res)=>{
      console.log(res)
      if(res.transaction.type ==='Deposit'){
        setResponse(`You funded your wallet with N${Numbro(res.transaction.amount)}`)
      }
      else if(res.transaction.type ==='Transfer'){
        setResponse(`you transferred N${Numbro(res.transaction.amount)} to ${details.data.email}`)
      }
      else if(res.transaction.type ==='Withdraw'){
        setResponse(`you withdrew N${Numbro(res.transaction.amount)} from your wallet`)
      }
      // update user balance on local storage
      setLoading(false)
      let newUser = {
        ...user,
        balance:res.balance
      }
      await storage.storeUser(newUser)
      //update user state globally
      setUser((preState)=>{
        return {
          ...preState,
          balance:res.balance
        }
      })

      setTransactions((preState)=>{
        return [
          res.transaction,
          ...preState
        ]
      })

      cb()

    }).catch((err)=>{
      setLoading(false)
      if(err.response){
        console.log(err.response)
        setErrMsg(err.response.statusText)  
      }else{
        console.log(err.message);
        setErrMsg(err.message)
      }
    })
  }

  const getTransactions = async () =>{
    setTxnLoading(true)
    request.getRequest(`${constants.baseURL}/api/v1/transaction/`)
    .then((res)=>{
      setTransactions(res.transactions)
      setTxnLoading(false)
    }).catch((err)=>{
      setTxnLoading(false)
      if(err.response){
        console.log(err.response)
        setErrMsg(err.response.statusText)  
      }else{
        console.log(err.message);
        setErrMsg(err.message)
      }
    })
  }

  const cancel = () =>{
    request.cancelRequest()
  }


  return {
    user,
    loginUser,
    logoutUser,
    errMsg,
    loading,
    signupUser,
    appLoad,
    transact,
    response,
    cancel,
    transactions,
    txnLoading,
    getTransactions
  };
  
}