import React, {createContext, useContext, useEffect} from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Link from '@mui/material/Link';
import Button from '@mui/material/Button'
import Divider from '@mui/material/Divider';
import MenuItem from '@mui/material/MenuItem';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import {Images} from '../../Images'
import {Link as RouteLink, useNavigate, useLocation} from "react-router-dom"
import {AuthContext} from '../../App'


const Navbar = () => {
  let auth = useContext(AuthContext);
  let navigate = useNavigate()
  let location = useLocation()


  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const Logout = () => {
    let { from } = location.state || { from: { pathname: "/" } };
    auth.logoutUser(()=>{
      navigate(from)
    })
  };


 
  return (
    <AppBar position="static" sx = {{background: "inherit"}}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
        <Container sx={{ flexGrow: 1, display: 'flex' }}>
            <img src = {Images.logo} width = '150px' />
        </Container>
        <Container sx={{ display: 'flex', flexGrow: 1, justifyContent: 'flex-end'}}>
            <Avatar alt={auth.user.lastName} src="/static/images/avatar/2.jpg" sx = {{bgcolor:'#46acbb'}} /> 
            <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                <ArrowDropDownIcon />
            </IconButton>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
                <MenuItem disabled>
            <Typography textAlign="center">Signed in as {auth.user.lastName} {auth.user.firstName}</Typography>
                </MenuItem>
                <Divider sx={{ my: 0.5 }} />
                <MenuItem onClick={Logout} sx = {{justifyContent:'center', color : 'red'}}>
                  Log out
                </MenuItem>

            </Menu>
        </Container>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default Navbar;
