import Button from '@mui/material/Button';

export default function CustomButton({label, ...rest}){
    return(
        <Button
              {...rest}
              type="submit"
              fullWidth
              variant="contained"
              sx={{ 
                  mt: 3, mb: 2, 
                  background : '#46acbb',
                  '&:hover': {
                    background: '#156494',
                  },
                }}
            >
             {label}      
        </Button>
    )
}