import React, { useState, useContext } from 'react'
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import {CustomTextField} from '../../Components/TextField';
import {AuthContext} from '../../App'
import InputAdornment from '@mui/material/InputAdornment';
import {useFormik} from 'formik'
import {depositSchema} from '../../Components/Formik';
import Alert from './Alert'
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Numbro from '../../Utilities/Numbro'
import {constants} from '../../Utilities/constants'

export default function DepositModal(props){
    let auth = useContext(AuthContext);

    const handleClose = () => {
        props.setOpen(false);
    };

    const [openAlert, setOpenAlert] = useState(false);

    
    const formik = useFormik({
        initialValues: {
          amount: ''
        },
        validationSchema: depositSchema,
        onSubmit: (data, {resetForm}) => {
            let apiLink = `${constants.baseURL}/api/v1/transaction/deposit`
            data.amount = parseInt(data.amount)
            let credentials = {data, apiLink}
            auth.transact(credentials, ()=>{
                props.setOpen(false);
                setOpenAlert(true)
                resetForm()
            })    
        },
    }); 
    
    return(
        <>
            <Dialog open = {props.open} >
                <DialogTitle sx= {{display: 'flex', justifyContent:'space-between'}}>
                    Deposit
                    <IconButton onClick={handleClose}>
                        <CloseIcon/>
                    </IconButton>
                </DialogTitle>
                <DialogContent>
                    <Box component = 'form' onSubmit={formik.handleSubmit} sx={{ mt: 1, width : '400px', p:3 }}>
                        <Typography>
                            {formik.values.amount && Numbro(formik.values.amount)}
                        </Typography>
                        <CustomTextField 
                            id = 'depositAmount'    
                            name = 'amount' 
                            label = 'Amount'
                            disabled = {auth.loading}
                            value={formik.values.amount}
                            onChange={formik.handleChange}
                            error={formik.touched.amount && Boolean(formik.errors.amount)}
                            helperText = {formik.touched.amount && formik.errors.amount} 
                            InputProps={{startAdornment: <InputAdornment position="start">N</InputAdornment>}}
                        />    
                        <Button disabled = {auth.loading} type="submit" >
                            {auth.loading?"Loading...":"Submit"}
                        </Button>
                        <Button  onClick={auth.cancel} sx = {{color: 'red'}} disabled = {!auth.loading}>Cancel</Button>
                    </Box>
                </DialogContent>
            </Dialog>
            <Alert open = {openAlert} setOpen = {setOpenAlert}/>
        </>
    )
}

