import React, {useState} from 'react'
import TransactionBtn from './TransactionBtn'
import MonetizationOnRoundedIcon from '@mui/icons-material/MonetizationOnRounded';
import CurrencyExchangeRoundedIcon from '@mui/icons-material/CurrencyExchangeRounded';
import CreditCardRoundedIcon from '@mui/icons-material/CreditCardRounded';
import DepositModal from './DepositModal'
import TransferModal from './TransferModal'
import WithdrawModal from './WithdrawModal'


export default function TransactionBtnGrp() {
    const [depositOpen, setDepositOpen] = useState(false)
    const [transferOpen, setTransferOpen] = useState(false)
    const [withdrawOpen, setWithdrawOpen] = useState(false)

 
    return (
        <>
            <div>
                <TransactionBtn
                    title = 'Deposit'
                    avatar = {<MonetizationOnRoundedIcon/>}
                    action = {setDepositOpen}
                />
                <DepositModal open = {depositOpen} setOpen = {setDepositOpen}/>    
            </div>
            <div>
                <TransactionBtn
                    title = 'Transfer'
                    avatar = {<CurrencyExchangeRoundedIcon/>}
                    action = {setTransferOpen}
                />
                <TransferModal open = {transferOpen} setOpen = {setTransferOpen}/>    
            </div>
            <div>
                <TransactionBtn
                    title = 'Withdraw'
                    avatar = {<CreditCardRoundedIcon/>}
                    action = {setWithdrawOpen}
                />
                <WithdrawModal open = {withdrawOpen} setOpen = {setWithdrawOpen}/>    
            </div>
        </>
    )
}
