import React, {useContext} from 'react';
import CustomButton from '../../Components/Button';
import {CustomTextField} from '../../Components/TextField'
import {PasswordField} from '../../Components/TextField'
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box'
import CustomForm from '../../Components/Form';
import { Link, useNavigate, useLocation} from "react-router-dom";
import {AuthContext} from '../../App'
import Alert from '@mui/material/Alert';
import {useFormik} from 'formik'
import {loginSchema} from '../../Components/Formik'
import {constants} from '../../Utilities/constants'




export default function Login(){
  let auth = useContext(AuthContext);
  let navigate = useNavigate();
  let location = useLocation()

  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    validationSchema: loginSchema,
    onSubmit: (data) => {
      console.log(data)
      let apiLink = `${constants.baseURL}/api/v1/auth/login`
      let credentials = {data, apiLink}
      let { from } = location.state || { from: { pathname: "/dashboard" } };
      auth.loginUser(credentials, ()=>{
        navigate(from)
      })    
    },
  });

  return(
    <CustomForm  title = 'Log In'>
      <Box component="form" onSubmit= {formik.handleSubmit} sx={{ mt: 1 }}>
        <CustomTextField 
          id = 'email' 
          name = 'email' 
          label = 'Email address' 
          type='email' 
          value={formik.values.email}
          onChange={formik.handleChange}
          error={formik.touched.email && Boolean(formik.errors.email)}
          helperText = {formik.touched.email && formik.errors.email}
        />
        <PasswordField
          id = 'password'
          name = 'password' 
          label = 'Password' 
          value={formik.values.password}
          onChange={formik.handleChange}
          error={formik.touched.password && Boolean(formik.errors.password)}
          helperText={formik.touched.password && formik.errors.password}
        />
        <FormControlLabel
          control={<Checkbox value="remember" sx = {{color: '#46acbb'}} />}
          label="Remember me"
        />
        <CustomButton label = {auth.loading?"Loading...":"Login"} disabled = {auth.loading}/>
        {auth.errMsg &&(
          <Box sx = {{mb : 2}}>
            <Alert variant="outlined" severity="error">
              {auth.errMsg}
            </Alert>
          </Box>
        )}
        <Grid container sx = {{justifyContent : 'space-between'}}>
          <Grid item >
            <Link to = "/"  className = 'link' variant="body2">
              Forgot password?
            </Link>
          </Grid>
          <Grid item>
            <Link to = "/signup" className = 'link'  variant="body2">Don't have an account? Sign Up</Link>
          </Grid>
        </Grid>
      </Box>
    </CustomForm>
  );
  
}