import {Box, Typography, Link, Chip} from '@mui/material'
import React, {useContext} from 'react';
import { DataGrid } from '@mui/x-data-grid';
import {AuthContext} from '../../App'
import Numbro from '../../Utilities/Numbro'
import moment from 'moment'

export default function Transactions(){
    const auth = useContext(AuthContext)

    const transactionColumns = [
        { field: 'id', headerName: 'ID', width: 90 },
        {
            field: 'type',
            headerName: 'Type',
            width: 150,
            renderCell: (params) => {
                if (params.value == null) {
                    return '';
                }
                const color = (params.value == 'Transfer')? 'pink' 
                : (params.value == 'Deposit')?'#5ee682'
                : (params.value == 'Withdraw') && '#f7ca59'
                return(
                    <Chip label={params.value} sx = {{bgcolor : color}} />
                )
            },
        },
        {
            field: 'amount',
            headerName: 'Amount',
            width: 150,
            renderCell: (params) => {
                if (params.value == null) {
                    return '';
                }
                return(
                    <span>
                        &#8358; {Numbro(params.value)}
                    </span>
                )
            }
        },
        
        {
            field: 'created_at',
            headerName: 'Created at',
            width: 250,
            valueFormatter: (params) => {
                if (params.value == null) {
                return '';
                }
                const valueFormatted = moment(params.value).format('MMMM Do YYYY, h:mm:ss a');
                return valueFormatted ;
            }   
        },

        {
            field: 'narrative',
            headerName: 'Narrative',
            width: 350,
        }
        
    ]
    
    return (
        <Box sx = {{p:5}}>
            <Box className sx={{ display: 'flex', justifyContent : 'start', mb : 3}}>
                <Typography variant = 'h4'>
                    Transactions
                </Typography>
            </Box>
            <div style={{ height:400, width: '100%' }}>
                <DataGrid
                    loading = {auth.txnLoading}
                    rows={auth.transactions}
                    columns={transactionColumns}
                    pageSize={10}
                    rowsPerPageOptions={[10]}
                    pagination
                />
            </div>
        </Box>
        
    )
}
